<template>
  <div class="application-center">
    <el-tabs
      v-model="initData.tabActive"
      class="tab-list"
      @tab-click="handleTabList"
    >
      <el-tab-pane
        v-for="item in tabList"
        :key="item.value"
        :label="item.label"
        :name="item.value"
      ></el-tab-pane>
    </el-tabs>
    <keep-alive>
      <component :is="initData.component"></component>
    </keep-alive>
  </div>
</template>
<script setup lang="ts">
import { markRaw, shallowReactive, defineAsyncComponent } from "vue";

const ServiceList = defineAsyncComponent(
  () =>
    import(
      "@/components/order-management/create-order/application-center/service-list.vue"
    )
);
const ResourceList = defineAsyncComponent(
  () =>
    import(
      "@/components/order-management/create-order/application-center/resource-list.vue"
    )
);
const AgentList = defineAsyncComponent(
  () =>
    import(
      "@/components/order-management/create-order/application-center/agent-list.vue"
    )
);
defineOptions({
  name: "orderManagement.createOrder.applicationCenter",
});
const initData = shallowReactive({
  tabActive: 0,
  component: markRaw(AgentList),
});
const tabList = [
  {
    label: "更多产品",
    value: 0,
    component: markRaw(AgentList),
  },
  {
    label: "已购应用",
    value: 1,
    component: markRaw(ServiceList),
  },
  {
    label: "已购服务",
    value: 2,
    component: markRaw(ResourceList),
  },
];
function handleTabList(value) {
  initData.tabActive = value.paneName;
  initData.component = tabList[value.paneName].component;
}
</script>

<style scoped lang="scss">
:deep(.el-tabs__header) {
  margin: 5px 0 0;
  .el-tabs__nav-wrap::after {
    height: 1px;
  }
}
</style>
